import React, {useState} from 'react';
import { IncreaseIcon, DecreaseIcon, CrossIcon } from '../../spiritIcons';
import {Typography, Grid} from '@mui/material';
import { styled } from "@mui/system";
import {dbt, getCookie} from '../../spiritUtils';

const WineImage = styled('img')`
    max-height: 201px;
    max-width: 62px;
  `

const styledLink = styled(Typography)`

`


const CartListItem = (props) => {
    const [count, setCount] = useState(1);

    const {data} = props
    const {image, name, price, sales, quantity, vendor, type, color} = data

    console.log(data);

    const handleIncreaseButtonClick = () => {
        setCount(count + 1)
    }

    const handleDecreaseeButtonClick = () => {
        if(count !== 1){
            setCount(count - 1)
        }
    }
    console.log(image)

    return (
        <Grid container>
            <Grid container item xs={5}>
                  <Grid item container xs={3} justifyContent="center">
                    <WineImage src={process.env.REACT_APP_BACKEND_DOMAIN + '/images/' + image} />
                  </Grid>
                  <Grid item container xs={9} direction="column" justifyContent="center">
                    <Grid item>
                      <Typography variant="body1" fontSize="14px">
                        {<b>{dbt(name)}</b>}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize="12px">
                        {dbt(color)} {dbt(type)}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize="12px">
                        Winery: {dbt(vendor)}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography component="a" variant="body1" fontSize="16px">
                        View in detail
                      </Typography>
                    </Grid>
                </Grid>
            </Grid>
            <Grid container item xs={6} >
                <Grid container item xs={12} md={4} justifyContent="center" alignItems="center">
                    <Typography component="div" fontSize="25px"><b>{price}$</b></Typography>
                </Grid>
                <Grid container item xs={12} md={8} justifyContent="center" alignItems="center">
                    <Grid item container justifyContent="center">
                        <IncreaseIcon/>
                        <Typography component="div" fontSize="25px" pl="29px" pr="29px">{count}</Typography>
                        <DecreaseIcon/>
                    </Grid>
                </Grid>
            </Grid>
            <Grid container item xs={1} justifyContent="center" alignItems="center">
                <CrossIcon />
            </Grid>
        </Grid>
    );
};

export default CartListItem;