import { useState, useEffect } from 'react';
import { Typography, Hidden, Grid } from "@mui/material";
import { styled } from "@mui/system";
import Footer from "../footer/Footer";
import Navbar from "../navbar/Navbar";
import { IncreaseIcon, DecreaseIcon } from '../../spiritIcons';
import SpiritButton from '../SpiritButton';
import {dbt, getCookie} from '../../spiritUtils';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import CartListItem from '../CartItemList/CartListItem';

const NavbarSpace = styled('div')`
    height: 75px;
    width: '100%';
`

const WineImage = styled('img')`
    max-height: 745px;
`

const Background = styled('div')`
    background-color: 
`

const ContentWrapper = styled('div')`
    max-width: 1770px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
`

const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`


function CartPage(props){
    const [cartItems, setCartItems] = useState(null);
    const [error, setError] = useState(null);


    useEffect(() => {
        const isloggedIn = sessionStorage.getItem('logged_in');
        const csrftoken = getCookie('csrftoken');
        if(isloggedIn){
            fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}cart`, {
              method: "GET",
              headers: {
                "Content-Type": "application/json;charset=utf-8",
                Authorization: `JWT ${localStorage.getItem("token")}`,
                "X-CSRFToken": csrftoken,
              },
            })
              .then((res) => {
                  if(res.ok){
                      return res.json()
                  } else {
                      setError('Something went wrong')
                      return false;
                  }
                })
              .then((cartData) => {
                if(!cartData) return;
                fetch(
                  `${process.env.REACT_APP_BACKEND_DOMAIN}/wineproduct/?wine=%27test%27`
                )
                  .then((res) => {
                    if(res.ok){
                        return res.json()
                    } else {
                        setError('Something went wrong')
                        return false;
                    }
                  })
                  .then((productData) => {
                    if(!productData) return;
                    let cart = [];
                    productData.menu.forEach((productItem) => {
                      cartData.items.forEach((cartItem) => {
                        if (productItem.id === cartItem.product_id) {
                          cart.push({
                            id: productItem.id,
                            image: productItem.image,
                            name: productItem.name,
                            sales: productItem.sales,
                            inStockQuantity: cartItem.quantity,
                            price: productItem.price,
                            type: productItem.type,
                            vendor: productItem.vendor,
                            color: productItem.color
                          });
                        }
                      });
                    });
                    setCartItems(cart);
                  });
              });
        }
    }, [])

    return (
      <>
        <div>
            <NavbarSpace></NavbarSpace>
            <Navbar fixed />
            <ContentWrapper>
                <Grid container>
                    <Grid item xs={12} md={9}>
                        <Grid item container xs={12}>
                            <Grid item xs={6} md={5}>
                                <Typography variant="body1" fontSize="25px" textAlign="center">Product</Typography>
                            </Grid>
                            <Hidden mdDown>
                            <Grid item xs={0} md={2}>
                                <Typography variant="body1" fontSize="25px" textAlign="center">Price</Typography>
                            </Grid>
                            </Hidden>
                            <Grid item xs={6} md={4}>
                                <Typography variant="body1" fontSize="25px" textAlign="center">Quantity</Typography>
                            </Grid>
                        </Grid>
                        <Grid item container xs={12} gap="20px">
                            {cartItems && cartItems.length > 0 && cartItems.map(item => <CartListItem key={item.id} data={item}/>)}
                            {error && <h1>{error}</h1>}
                        </Grid>
                    </Grid>
                    <Grid item xs={12} md={3} sx={{backgroundColor: 'red'}}>2</Grid>
                </Grid>
            </ContentWrapper>
        </div>
      </>
    );
}

export default CartPage;