import {useState} from 'react';
import { Typography, Hidden, Grid, Box } from "@mui/material";
import { styled } from "@mui/system";


const TypeSelector = (props) => {
    const {types, handleFilters} = props
    const [active, setActive] = useState(null)

    const StyledSelector = styled('div')`
        width: 100%;
        height: 60px;
        background-color: ${active ? '#202020' : '#f2f2f2'};
        color: ${active ? '#fbfbfb' : '#202020' };
        padding-top: 10px;
        padding-left: 15px;
        ${active ? `
            &:hover {
                cursor: pointer;
            }
        ` : `
            &:hover{
                border-left: solid 3px #202020;
                cursor: pointer;
            }
        `}
    `;

    // const StyledSelector = styled('div')(({active}) => (`
    //     width: 100%;
    //     height: 60px;
    //     background-color: ${active ? '#202020' : '#f2f2f2'};
    //     color: ${active ? '#fbfbfb' : '#202020' };
    //     padding-top: 10px;
    //     padding-left: 15px;
    //     ${active ? `
    //         &:hover {
    //             cursor: pointer;
    //         }
    //     ` : `
    //         &:hover{
    //             border-left: solid 3px #202020;
    //             cursor: pointer;
    //         }
    //     `}
    // `));


    const handleClick = (event) => {
        setActive(event.currentTarget.textContent)
    }


    return (
        <>
            {
            types.map(item => 
                <StyledSelector onClick={handleClick} key={item}>
                    <Typography variant="body1" fontSize="25px">{item}</Typography>
                </StyledSelector>
            )
            }
        </>
    )
}

export default TypeSelector;